// frontend/pages/create-inspection.js
import { useState } from 'react';
import StandardDropdown from '../components/StandardDropdown'
import axios from 'axios';
export default function CreateInspection() {
  const [form, setForm] = useState({
    name: '',
    standard: '',
    note: '',
    price: '',
    samplingPoint: [],
    samplingDate: '',
    file: null,
  });
  const [selectedStandard, setSelectedStandard] = useState('');  
  const samplingPoints = ['Front End', 'Back End', 'Other'];
  
  const handleChange = (e) => {
    const { name, value, type } = e.target;

    setForm((prevForm) => ({
      ...prevForm,
      [name]: type === 'number' ? parseFloat(value) || '' : value,
    }));
  };

  const handleCheckboxChange = (point) => {
    setForm((prevForm) => ({
      ...prevForm,
      samplingPoint: prevForm.samplingPoint.includes(point)
        ? prevForm.samplingPoint.filter((p) => p !== point)
        : [...prevForm.samplingPoint, point],
    }));
  };

  const handleFileChange = (e) => {
    setForm((prevForm) => ({
      ...prevForm,
      file: e.target.files[0],
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append('name', form.name);
    formData.append('createDate', new Date().toISOString());
    formData.append('standardID', form.standard);
    formData.append('note', form.note);
    formData.append('standardName', selectedStandard);
    formData.append('samplingDate', form.samplingDate);
    formData.append('samplingPoint', form.samplingPoint);
    formData.append('price',form.price)

    if(form.file){
      formData.append('file', form.file);
    }

    try{
      const response = await axios.post('http://localhost:8000/api/history', formData,{
        headers:{
          'Content-Type': 'multipart/form-data'
        }
      })
      if(response.status === 201){
        console.log('Data save')
      }
    }catch(err){
      console.error('Error submitting form',err)
    }
  };

  return (
    <div>
      <h1>Create Inspection</h1>
      <form onSubmit={handleSubmit}>
        {/* Name */}
        <label>
          Name*
          <br/>
          <input
            type="text"
            name="name"
            value={form.name}
            onChange={handleChange}
            placeholder='Please Holder'
            required
          />
        </label>
        <br />

        {/* Standard */}
        <StandardDropdown setSelectedStandard={setSelectedStandard} />

        <br />

        {/* File Upload */}
        <label>
          Upload File:
          <input
            type="file"
            accept=".json"
            onChange={handleFileChange}
            placeholder='raw1.json'
          />
        </label>
        <br />
        {/* Note */}
        <label>
          Note:
          <input
            type="text"
            name="note"
            value={form.note}
            onChange={handleChange}
          />
        </label>
        <br />

        {/* Price Field */}
        <label>
          Price:
          <input
            type="number"
            name="price"
            value={form.price}
            placeholder='10,000'
            min="0"
            max="100000"
            step="0.01"
            onChange={handleChange}
          />
          <small> (0 - 100,000, up to 2 decimal places)</small>
        </label>
        <br />

        {/* Sampling Point Checkboxes */}
        <fieldset>
          <legend>Sampling Point:</legend>
          {samplingPoints.map((point) => (
            <label key={point}>
              <input
                type="checkbox"
                value={point}
                checked={form.samplingPoint.includes(point)}
                onChange={() => handleCheckboxChange(point)}
              />
              {point}
            </label>
          ))}
        </fieldset>
        <br />

        {/* Sampling Date/Time */}
        <label>
          Date/Time of Sampling
          <br/>
          <input
            type="datetime-local"
            name="samplingDate"
            value={form.samplingDate}
            onChange={handleChange}
          />
          <small> (Format: DD/MM/YYYY HH:mm:ss)</small>
        </label>
        <br />
        {/* Submit Button */}
        <button type="submit">Submit</button>
      </form>
    </div>
  );
}